import React from 'react';
import HTMLFlipBook from 'react-pageflip';

const Flipbook = () => {
  const images = Array.from({ length: 84 }, (_, i) => `/images/page${i + 1}.png`);

  return (
    <HTMLFlipBook
      width={550}
      height={733}
      size="stretch"
      minWidth={315}
      maxWidth={1000}
      minHeight={420}
      maxHeight={1350}
      maxShadowOpacity={0.5}
      showCover={true}
      mobileScrollSupport={false}
    >
      {/* Single first page */}
      <div className="page">
        <img src={process.env.PUBLIC_URL + images[0]} alt="Page 1" />
      </div>

      {/* Double pages */}
      {images.slice(1, -1).map((image, index) => (
        <div key={index} className="page">
          <img src={process.env.PUBLIC_URL + image} alt={`Page ${index + 2}`} />
        </div>
      ))}

      {/* Single last page */}
      <div className="page">
        <img src={process.env.PUBLIC_URL + images[images.length - 1]} alt={`Page ${images.length}`} />
      </div>
    </HTMLFlipBook>
  );
};

export default Flipbook;


